<template>
	<div class="range-wrapper">
		<div class="range">
			<label :for="rangeId" class="strong color-gray small" v-if="label">{{ label }}:</label>
			<input
				type="range"
				:id="rangeId"
				:min="min"
				:max="max"
				v-model="value"
				@mouseup="setValue"
				@mousemove="exposeTooltip"
				@mousedown="exposeTooltip"
			/>
			<div class="tooltip" ref="tooltip">{{ value }}</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Range",

		props: {
			min: {
				type: Number,
				default: 0,
			},
			max: {
				type: Number,
				default: 10
			},
			defaultValue: {
				type: Number,
				default: 1
			},
			label: {
				type: String
			}
		},

		data() {
			return {
				value: this.defaultValue || 1,
			}
		},

		methods: {
			/**
			 */
			exposeTooltip() {
				const leftIndent = Math.round((this.value - this.min) * 100) / (this.max - this.min);
				const pixelK = Math.round(14 * leftIndent / 100); // 14px - ширина ползунка

				this.$refs.tooltip.style.left = `calc(${leftIndent}% - ${pixelK}px - 6px)`;
			},
			/**
			 */
			setValue() {
				this.exposeTooltip();
				this.$emit('commit', this.value);
			},
		},

		computed: {
			/**
			 * @return {string}
			 */
			rangeId() {
				return 'range_' + (new Date());
			}
		},

		mounted() {
			this.exposeTooltip();
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.range-wrapper {
		padding: 0.5rem 0;

		.range {
		 position: relative;
		 width: 100%;

		 .tooltip {
			 display: none;
			 top: -10px;
			 @tooltipSize: 28px;
			 position: absolute;
			 width: @tooltipSize;
			 height: @tooltipSize;
			 line-height: @tooltipSize;
			 text-align: center;
			 background-color: @color-dark;
			 border-radius: 4px;
			 color: white;
			 font-size: 14px;
			 font-weight: bold;
		 }

		 &:hover {
			 .tooltip {
					display: block;
			 }
		 }

		 input[type="range"] {
			 -webkit-appearance: none;
			 width: 100%;
			 height: 6px;
			 background-color: @color-beige;
			 border-radius: 3px;
			 opacity: 0.8;
			 transition: opacity .2s linear;
			 cursor: pointer;

			 &:hover {
				 opacity: 1;
			 }

			 &::-webkit-slider-thumb {
				 -webkit-appearance: none;
				 appearance: none;
				 width: 14px;
				 height: 14px;
				 border-radius: 50%;
				 background-color: @color-beige;
				 cursor: pointer;
				 -webkit-box-shadow: 0 0 2px 1px @color-dark;
				 -moz-box-shadow: 0 0 2px 1px @color-dark;
				 box-shadow: 0 0 2px 1px @color-dark;
			 }

			 &::-moz-range-thumb {
				 width: 14px;
				 height: 14px;
				 border-radius: 50%;
				 background-color: @color-beige;
				 -webkit-box-shadow: 0 0 2px 1px @color-dark;
				 -moz-box-shadow: 0 0 2px 1px @color-dark;
				 box-shadow: 0 0 2px 1px @color-dark;
			 }
		 }
		}
	}
</style>


